/**
 * Loading 提示弹层
 *      text        提示文字
 *      icon        提示图标
 */

import React from 'react';
import './Loading.css';

export default class Loading extends React.Component {
    static defaultProps = {
        text: '',
        icon: require('../assets/loading.gif'),
    };
    constructor(props) {
        super(props);
        this.state = {

        }
    }
    render() {
        return (
            <div className="maskContent">
                <div className="maskBox">
                    <img src={this.props.icon} alt="" className="img" />
                    {this.props.text ? <p className="text">{this.props.text}</p> : null}
                </div>
            </div>
        );
    }
}

